class TilesController {

    //Bp : BreakPoint
    constructor (smallBp , mediumBp , bigBp , naturalSizes , maxCache) {

        
        this.breakPoints = {
            big : bigBp,
            medium : mediumBp,
            small : smallBp
        }

        // the preloaded size by default (this is loaded when the pages load)
        this.preloadedBP = smallBp;

        //the original images sizes
        this.naturalSizes = naturalSizes;
        this._currentNaturalWidth;

        this.maxCache = maxCache;

        this._currentFrame = 0;
        this._currentBreakPoint;
        this._scaleFactor;

        //the cache array , contain lazy-loaded images width additional info
        this.preloadedFrames = new Array();

        //this.timeOutSplit = null;

        this.init();
    }

    init() {
    }

    fetchTiles(currentWidth , frameIndex , scaleFactor) {

        this._setCurrentBP(currentWidth);
        this._setCurrentNaturalSize(this._currentBreakPoint);

        if(currentWidth <= this.preloadedBP) return this._loadTiles(true);

        
        this._currentFrame = frameIndex;
        this._scaleFactor = scaleFactor;


        if(!this.preloadedFrames.length) {
            //first fetch
            console.log("first fetch");

            return this._loadTiles();
        }


        const preloaded = this.preloadedFrames.find((v) => {
            if(v.frame === this._currentFrame && v.breakPoint === this._currentBreakPoint) {
                return v;
            }
        });

        if(typeof preloaded !== "undefined") {
            //console.log(preloaded);
            return this._loadExistingTiles(preloaded);
        }

        return this._loadTiles();
        
    }


    _loadExistingTiles(preloaded){
        
        return new Promise((resolve , reject) => {
            if(preloaded.frame === this._currentFrame && preloaded.breakPoint === this._currentBreakPoint) {
                resolve(preloaded.canvas);
            }else{
                reject();
            }
        });
    }

    _loadTiles(error = false) {

        //window.clearTimeout(this.timeOutSplit);
        //console.time("test");
        const p = new Promise((resolve , reject ) => {
            if(error) {
                //window.clearTimeout(this.timeOutSplit);
                reject();
                
            }else{

                const folder = this._getTilesFolder(this._currentFrame , this._currentBreakPoint);
                const index = this._currentFrame;
                const breakpoint = this._currentBreakPoint;
                const scaleFactor = this._scaleFactor;
                this._loadSplitImagesAll(folder , index , breakpoint , scaleFactor, (WSCanvas , id , bp , sf) => {

                    const tilesInfo = {
                        frame : id,
                        breakPoint : bp,
                        canvas : WSCanvas
                    };

                    this._addToCache(tilesInfo);

                    if(!this._canLoadImg(id , bp , sf)) {
                        //console.log("skipped frame");
                        reject();
                        return;
                    }
                    resolve(WSCanvas);
                });
            }
            
        });
        return p;
        
    }

    
    _addToCache(cacheInfo) {
        
        if(this.preloadedFrames.includes(cacheInfo)) return;

        this._checkCache();
        this.preloadedFrames.push(cacheInfo);
    }
    

    _loadSplitImagesAll(folder , id , breakpoint , scaleFactor , onFinish) {

        //console.log(folder);

        const natWidth = this._currentNaturalWidth;
        const natHeight = this._calculateWithAspectRatio(this._currentNaturalWidth);

        let canvas = document.createElement('canvas');
        let ctx = canvas.getContext("2d");


        canvas.width = natWidth;
        canvas.height = natHeight;

        
        
        const size = 512; // the crop rect size

        //console.log(this._currentNaturalWidth);
        
        const partsX = Math.floor(natWidth / size);
        const partsY = Math.floor(natHeight / size);

        const moduloX = natWidth % size;
        const moduloY = natHeight % size;

        const lengthX = moduloX != 0 ? partsX+1 : partsX;
        const lengthY = moduloY != 0 ? partsY+1 : partsY;

        let allPromises = new Array();

        for(let y= 0 ; y < lengthY ; y++) {
            for(let x = 0 ; x < lengthX ; x++ ) {

                const p = new Promise((resolve , reject) => {
                    const sx = x*size , 
                    sy = y*size;
                    
                    const index = lengthX * y + x;
                    const src = `${folder}p-${index}.jpg` ;
                    const path = require("@/" + src);
    
                    const img = new Image();
                    img.src = path;
                    img.onload = () => {
                        ctx.drawImage(img, sx, sy);
                        resolve();
                    };

                    img.onerror = () => {
                        reject();
                    }
                    
                    
                });

                allPromises.push(p);

            }
        }

        
        
        Promise.all(allPromises).then(() => {
            onFinish(canvas , id , breakpoint , scaleFactor);
        })
    }

    _canLoadImg(frame , breakPoint , scaleFactor) {

        if(
            frame !== this._currentFrame || 
            breakPoint !== this._currentBreakPoint || 
            scaleFactor !== this._scaleFactor
        )
        {
            return false;
        }
        return true;
    }

    async _loadSplitImages(folder , id , breakpoint , scaleFactor , onFinish) {

        //console.log(folder);

        const natWidth = this._currentNaturalWidth;
        const natHeight = this._calculateWithAspectRatio(this._currentNaturalWidth);

        let canvas = document.createElement('canvas');
        let ctx = canvas.getContext("2d");


        canvas.width = natWidth;
        canvas.height = natHeight;

        
        
        const size = 512; // the crop rect size

        //console.log(this._currentNaturalWidth);
        
        const partsX = Math.floor(natWidth / size);
        const partsY = Math.floor(natHeight / size);

        const moduloX = natWidth % size;
        const moduloY = natHeight % size;

        const lengthX = moduloX != 0 ? partsX+1 : partsX;
        const lengthY = moduloY != 0 ? partsY+1 : partsY;

        

        for(let y= 0 ; y < lengthY ; y++) {
            for(let x = 0 ; x < lengthX ; x++ ) {

                const sx = x*size , 
                sy = y*size;
                
                const index = lengthX * y + x;
                const src = `${folder}p-${index}.jpg` ;
                const path = require("@/" + src);

                const img = new Image();
                img.src = path;
                await img.decode();
                ctx.drawImage(img, sx, sy);

                if( y >= lengthY-1 && x >= lengthX-1 ) {
                    onFinish(canvas , id , breakpoint , scaleFactor);
                    canvas = ctx = null;
                }
                /* img.onload = () => {
                    
                } */

            }
        }
    }

    _draw(ctx , img , sx , sy) {
        ctx.clearRect(sx, sy, ctx.canvas.width, ctx.canvas.height);
        ctx.drawImage(img, sx, sy);
    }


    _getTilesFolder(frameIndex , size){

        let folder = "assets/tiles";
        switch (size) {
            case this.breakPoints.medium:
                folder += "/medium-cropped/00_" + frameIndex; //for test setting folder to big tiles
                break;
            case this.breakPoints.big:
                folder += "/big-cropped/00_" + frameIndex;
                break;
        
            default:
                break;
        }
        return folder + "/";
    }

    _setCurrentNaturalSize(size) {
        switch (size) {
            case this.breakPoints.medium:
                this._currentNaturalWidth = this.naturalSizes.medium;
                break;
            case this.breakPoints.big:
                this._currentNaturalWidth = this.naturalSizes.big;
                break;
        
            default:
                break;
        }
    }

    _setCurrentBP(width){

        Object.values(this.breakPoints).forEach(bp => {
            if(bp >= width) {
                this._currentBreakPoint = bp;
                return;
            }
        });

    }

    _checkCache(){
        if(this.preloadedFrames.length > this.maxCache) {
            this.preloadedFrames.shift();
            console.log("removed from array");
        }
    }


    _calculateWithAspectRatio(width) {
        const aspect = this.naturalSizes.maxSize.w / this.naturalSizes.maxSize.h;
        const height = width / aspect;

        return height;
    }




    

    
}


export {TilesController}