import { Img } from "./img.js"

class Animation{

    images = [];
    constructor(fileNameTemplate , countSprite) {

        this.countSprite = countSprite;
        this.currentIndex = 0;
        this.countLoaded = 0;
        this.errors = [];

        for (let i = 0; i < this.countSprite; i++) {

            const reg = i.toLocaleString('en-US', {minimumIntegerDigits: 4, useGrouping:false});
            const path = fileNameTemplate.replace('?' , reg);

            const imgPath = require('@/' + path);
            const image = new Img(imgPath);
            this.images.push(image);
            //console.log(this.images[i]);
            image.source.addEventListener('OnLoad' , () => {
                this.countLoaded++;
            } , {once:true})
            image.source.addEventListener('OnError' , (e) => {
                this.errors.push(e);
            })
        }
    }

    isReady() {
        return (this.countLoaded == this.countSprite);
    }

    getCurrentFrame() {
        return this.images[this.currentIndex].source;
    }

    setCurrentFrame(index) {
        this.currentIndex = index;
    }

    next(){
        
        this.currentIndex++;
        if (this.currentIndex >= this.countSprite) {
            this.currentIndex = 0;
        }
    }

    prev(){
        this.currentIndex--;
        if (this.currentIndex < 0) {
            this.currentIndex = this.countSprite-1;
        }
    }

}


export { Animation };