import { Animation } from "./animation.js"
import { Hotspots } from "./hotspots.js";
import { TilesController } from "./tilesController.js";
import { Zoomer } from "./zoomer.js";

class Animator{

    constructor (canvasGroup , animationsPattern , countFrames , x , y , speed , wImg , hImg , hotspotRegions) {
        this.canvasGroup = canvasGroup;
        this.tilesCanvas = canvasGroup.tilesCanvas;
        this.animCanvas = canvasGroup.animation;
        this.htRefCanvas = canvasGroup.hotspotsRef;
        this.eventsCanvas = canvasGroup.eventsCanvas;
        this.animationsPattern = animationsPattern;
        this.countFrames = countFrames;
        this.x = x;
        this.y = y;
        this.speed = speed;
        this.refWidthImg = wImg;
        this.refHeightImg = hImg;
        this.wImg = 0;
        this.hImg = 0;
        this.hotspotRegions = hotspotRegions;

        
        this.animations = [];
        

        this.animationIndex = 0;
        this.reverse = false;
        this.stopTimeline = false;
        this.isPlaying = false;
        this.frame;
        this.frameIndex = 0;
        this.animationsReady = false;
        this.errorLoading = false;
        
        this.startTime;
        this.drawStartTime = 0;

        this.hotspots;

        this._tilesController;

        this.init();
        this.load();
    }

    init() {
        
        this.animCtx = this.animCanvas.getContext("2d");
        this.tilesCtx = this.tilesCanvas.getContext("2d");

        const naturalImagesSize = {
            maxSize : {w:1920,h:1080},
            big : 1920,
            medium : 1200,
        }
            
        const maxTilesCache = 100;
        this._tilesController = new TilesController( 512 , 1000 , 1300 , naturalImagesSize , maxTilesCache);
    }

    load() {

        for (let i = 0; i < this.animationsPattern.length; i++) {
            const pattern = this.animationsPattern[i];
            const anim = new Animation(pattern , this.countFrames);
            this.animations.push(anim);
        }
        
        console.log("loading..");
        const loader = setInterval(() => {
            
            for (let i = 0; i < this.animations.length; i++) {
                const anim = this.animations[i];
                if(anim.errors.length > 0){
                    this.errorLoading = true;
                    clearInterval(loader);
                    throw new Error('Error while loading animations! please check your images...');
                }
            }
            if(this.animatorReady()){
                clearInterval(loader);

                console.log("all animations loaded!");

                this.hotspots = new Hotspots(this.canvasGroup, this.hotspotRegions);

                window.dispatchEvent(new CustomEvent("OnAppReady", {
                    bubbles: false
                }));

                this.zoomer = new Zoomer(this.canvasGroup.eventsCanvas);
                this.zoomer.rect.addEventListener('OnZoom' , () => {
                    this.onZoomEvent();
                })
            }
        } , 100);
        
    }

    run() {

        this.animationsReady = true;
        this.showAnimationFrame(this.animationIndex , this.frameIndex);
        
    }

    play() {
        if(this.isPlaying)
            return;

        window.requestAnimationFrame( (timestamp) => {this.update(timestamp)} );
        this.stopTimeline = false;
        this.isPlaying = true;
    }

    stop() {
        if(this.stopTimeline) return;
        window.cancelAnimationFrame( (timestamp) => {this.update(timestamp)} );
        this.stopTimeline = true;
        this.isPlaying = false;
    }

    replay(delay) {
        this.stop();
        setTimeout(() => {this.play()} , delay);
        return;
    }

    update(timestamp) {

        if(!this.animationsReady) {
           return;
        }
        
        if (this.startTime === undefined) {
            this.startTime = timestamp;
        }
        const elapsed = timestamp - this.startTime;
    
        if(elapsed >= this.speed && !this.stopTimeline) {
            this.step();
            this.startTime = timestamp;
        }

        window.requestAnimationFrame( (timestamp) => {this.update(timestamp)} );

    }

    step(reverse){
        reverse = (reverse == undefined) ? this.reverse : reverse;
        const anim = this.getAnimation();
        if(reverse) {
            anim.prev();
        }else{
            anim.next();
        }
        this.showAnimationFrame(this.animationIndex , anim.currentIndex);
    }

    draw(){

        if(!this.animationsReady) {
            return;
        }

        /* const timestamp = Date.now();
        if (this.drawStartTime === undefined) {
            this.drawStartTime = timestamp;
        }
        const elapsed = timestamp - this.drawStartTime;
    
        if(elapsed <= 16.66) { //framerate simulation must refactor 
            return;
            
        }

        this.drawStartTime = timestamp; */

        
        const anim = this.getAnimation();
        this.frame = anim.getCurrentFrame();
        const scaleFactor = this.zoomer.currentScale;

        this.wImg = Math.round(this.refWidthImg * scaleFactor);
        this.hImg = Math.round(this.refHeightImg * scaleFactor);

        const x = Math.round( (this.animCanvas.width*0.5) - (this.refWidthImg*0.5) * scaleFactor );
        const y = Math.round( (this.animCanvas.height*0.5) - (this.refHeightImg*0.5) * scaleFactor );

        this.tilesCtx.clearRect(0, 0, this.tilesCanvas.width, this.tilesCanvas.height);
        this.animCtx.clearRect(0, 0, this.animCanvas.width, this.animCanvas.height);

        this.animCtx.drawImage(this.frame , x, y , this.wImg , this.hImg);
        this.frameIndex = anim.currentIndex;
        this.hotspots.drawHts(this.animationIndex , this.frameIndex , this.wImg , this.hImg);
        
        const pr = this._tilesController.fetchTiles(this.wImg , this.frameIndex , scaleFactor);
        pr.then(canvas => {
            this.tilesCtx.drawImage(canvas , x, y , this.wImg , this.hImg);
        }).catch(() => {
            return;
        });
    }

    switchAnimation(index) {
        if(index < 0 || index >= this.animations.length) {
            return;
        }

        this.showAnimationFrame(index , this.frameIndex);
    }

    showAnimationFrame (animIndex , frameIndex) {
        this.animationIndex = animIndex;
        const anim = this.getAnimation();
        anim.setCurrentFrame(frameIndex);
        this.requestFrameDraw();
    }

    requestFrameDraw() {
        window.requestAnimationFrame( () => {this.draw()} );
    }

    playFrame(reverse) {
        this.stop();
        this.step(reverse);
    }


    getAnimation() {
        return this.animations[this.animationIndex];
    }


    animatorReady() {
        let countLoaded = 0;
        let countSprite = 0;
        for (let i = 0; i < this.animations.length; i++) {
            const anim = this.animations[i];
            countLoaded += anim.countLoaded;
            countSprite = anim.countSprite;
        }

        return countLoaded == (countSprite * this.animations.length)
    }


    onZoomEvent(){
        this.stop();
        this.requestFrameDraw();
    }

}


export { Animator };