import { COLOR_HELPER} from "../utils/helpers";
import REGIONS from '@/assets/regions.json'

class Hotspots{

    constructor(canvasGroup , hotspotRegions)
    {
        this.canvasGroup = canvasGroup;
        
        this.hotspotRegions = hotspotRegions;

        this.imgW, this.imgH , this.level , this.frame;

        this.htDrawCtx , this.htRefCtx;

        this.filtredRegions = [[[]]];
        
        this._uColorSet;

        this._drawInterval;
        this._drawTimestamp;

        this.filterOption = {};

        this.init();
    }

    init(){
        
        this.htDrawcanvas = this.canvasGroup.hotspotsDraw;
        this.htRefCanvas = this.canvasGroup.hotspotsRef;
        this.htEvtCanvas = this.canvasGroup.eventsCanvas;


        this.htDrawCtx = this.htDrawcanvas.getContext("2d");
        this.htRefCtx = this.htRefCanvas.getContext("2d" , {willReadFrequently: true }); // For more info about the [willReadFrequently] : https://html.spec.whatwg.org/multipage/canvas.html#dom-canvasrenderingcontext2dsettings-willreadfrequently
        this.htEvtCtx = this.htRefCanvas.getContext("2d");
        
        
        this._drawInterval = 3;
        
        this.setupFromXml();
        this.drawHts();
        this.hSEvents();
        
    }
    
    setupFromXml() {
        this._uColorSet = new Set();
        
        this.hotspotRegions.forEach(ht => {
            

            const polyColor = ht.$.backgroundcolor.replace("0x" , "#");
            //const polyAlpha = ht.$.backgroundalpha;
            const polyAlpha = 0.4;
            const name = ht.$.id;

            ht.polygon.forEach(p => {

                const poly = p.$;
                
                //console.log(poly)
                //const row = parseInt(poly.row);
                const row = parseInt(poly.state);
                const column = parseInt(poly.column);

                const fPos = this._formatPositions(poly.vertices);

                let uniqColor = COLOR_HELPER.getUniqColor(this._uColorSet);
                this._uColorSet.add(uniqColor);
                uniqColor = COLOR_HELPER.hexToRgbA(uniqColor);
                
                const polyObj = {
                    name : name,
                    polygon : fPos,
                    colorRef : uniqColor,
                    fillColor : polyColor,
                    alpha : polyAlpha,

                }

                const uKey = COLOR_HELPER.getColorString(uniqColor);

                if(typeof(this.filtredRegions[row]) == "undefined") {
                    this.filtredRegions[row] = [];
                }
                if(typeof(this.filtredRegions[row][column]) == "undefined") {
                    this.filtredRegions[row][column] = [];
                }
                this.filtredRegions[row][column][uKey] = polyObj;
            })
                        

        })

        this.hotspotRegions.length = 0;

    }


    drawHts(level , frame , imgW , imgH) {
        this.level = level;
        this.frame = frame;
        this.imgW = imgW;
        this.imgH = imgH;
        this._drawHts();
    }
    _drawHts() {
        this.drawHotspots();
        this.drawHotspotsRef();
    }

    drawHotspots() {

        this._clearCtx(this.htDrawCtx);
        const region = this._getCurrentRegion();

        if(region == undefined) return;
        
        Object.values(region).forEach((poly) => {
            
            if(this.validate(poly.name)) {
                const positions = poly.polygon;
                const c = COLOR_HELPER.getColorFill(COLOR_HELPER.hexToRgbA(poly.fillColor) , poly.alpha);
                this._drawPolygon(this.htDrawCtx , positions , c , '#CECECE');
            }
            
        })

    }


    drawHotspotsRef() {
        const region = this._getCurrentRegion();
        if(region == undefined) return

        this._clearCtx(this.htRefCtx);

        Object.values(region).forEach((poly) => {
            
            if(this.validate(poly.name)) {
                const positions = poly.polygon;
                const fill = COLOR_HELPER.getColorFill(poly.colorRef , 1);
    
                requestAnimationFrame(() => this._drawPolygon(this.htRefCtx , positions , fill , 'navy'));
            }

        })

    }

    _drawPolygon(ctx , positions , fillStyle , strokeStyle) {

        ctx.strokeStyle = strokeStyle;
        ctx.lineWidth = 1.0;
        ctx.fillStyle = fillStyle;

        ctx.beginPath();
        for (let i = 0; i < positions.length; i++) {
            const points = positions[i];

            const margX = Math.round( (ctx.canvas.width - this.imgW) * 0.5 );
            const margY = Math.round( (ctx.canvas.height - this.imgH) * 0.5 );

            //console.log(margX , margY);

            const x = (points.x * this.imgW) + margX;
            const y = points.y * this.imgH + margY;

            ctx.lineTo(x ,y);
        }

        ctx.fill();
        ctx.closePath();
    }


    validate(regionName) {
        const infoPoly = REGIONS[regionName];  
        if(infoPoly != undefined) {
            const surface = parseInt( infoPoly.surface , 10 );
            const filterOpt = this.filterOption.surface;

            if(filterOpt == undefined) return false;

            if(surface < filterOpt.minSurface || surface > filterOpt.maxSurface) {
                return false;
            }
            return true;
        }
        return false;
    }

    setSurfaceFilter(surface) {
        this.filterOption.surface = {
            minSurface : surface.min,
            maxSurface : surface.max,
        }
        this._updateHotspots();
    }

    _updateHotspots() {
        const timestamp = Date.now();

        if(this._drawTimestamp == undefined) {
            this._drawTimestamp = timestamp
            return;
        }

        const elapsed =  timestamp - this._drawTimestamp;

        if(elapsed > this._drawInterval) {
            this._drawHts();
            this._drawTimestamp = timestamp;
        }
        
    }
    


    

    _formatPositions(pos){
        let positions = [];
        const reg = pos;
        const polys = reg.split('|');
        for (let i = 0; i < polys.length; i++) {
            const coor = polys[i].split(";");
            
            for (let j = 0; j < coor.length; j++) {
                const obj = coor[j].split(",");
                positions.push({
                    x : obj[0],
                    y : obj[1],
                })
            }
            }
        return positions;
    }
    
    



    hSEvents() {

        this.htEvtCanvas.addEventListener("mousemove" , (e) => {
            this.mouseMoveEvent(e);
        });
    }

    mouseMoveEvent(e) {

        const bounding = this.htDrawcanvas.getBoundingClientRect();
        const x = Math.round(e.clientX - bounding.left);
        const y = Math.round(e.clientY - bounding.top);

        const pixel = this.htRefCtx.getImageData(x, y, 1, 1);
        const data = pixel.data;

        if( data[0] == 0 && data[1] == 0 && data[2] == 0 ){
            
            this._regionHoverOut();
            return;
        }
        
        this._regionHoverState(data , {x : e.clientX , y : e.clientY});
        
    }

    _regionHoverState(color , mousePos) {

        const region = this._getCurrentRegion();
        if(region == undefined) return;
        const c = {red : color[0] , green:color[1] , blue :color[2] }
        const cString = COLOR_HELPER.getColorString(c);
        const poly = region[cString];
        if(poly == undefined) {
            return;
        }

        //console.log('Hovered ' + region[cString].name);
        this.htEvtCanvas.style = "cursor : pointer";

        window.dispatchEvent(new CustomEvent("OnHtHover", {
            bubbles: false,
            detail : {
                mousePos : mousePos,
                region : region[cString]
            }
        }));
    }

    _regionHoverOut(){
        this.htEvtCanvas.style = "cursor : default";
        window.dispatchEvent(new CustomEvent("OnHtHoverOut", {
            bubbles: false,
        }));
    }


    _getCurrentRegion(){

        if(typeof(this.filtredRegions[this.level]) == "undefined") {
            this._clearCtx(this.htRefCtx);
            return undefined;
        }

        if(typeof(this.filtredRegions[this.level][this.frame]) == "undefined") {
            this._clearCtx(this.htRefCtx);
            return undefined;
        }

        return this.filtredRegions[this.level][this.frame];
    }

    _clearCtx(ctx) {
        ctx.clearRect(0, 0, ctx.canvas.width, ctx.canvas.height);
    }
        
}


export { Hotspots}