class CanvasBuilder {

    constructor (parentId) {
        this.parentId = parentId;
        this.parentElem;
        this.canvasGroup;
    }

    init(callback) {
        this.parentElem = document.getElementById(this.parentId);
        if(this.parentElem == undefined) {
            alert("Orbital dom element not found !! " + this.parentId);
            throw new Error ("Orbital dom element not found !!" + this.parentId);
        }

        const tilesCanvas = this.createCanvas("tilesCanvas");
        const animCanvas = this.createCanvas("animCanvas");
        const htRefCanvas = this.createCanvas("htRefCanvas");
        const htDrawCanvas = this.createCanvas("htDrawCanvas");
        const eventsCanvas = this.createCanvas("eventsCanvas");

        this.canvasGroup = {
            animation :  animCanvas,
            hotspotsRef : htRefCanvas,
            hotspotsDraw : htDrawCanvas ,
            eventsCanvas : eventsCanvas,
            tilesCanvas : tilesCanvas
        }
        callback(this.canvasGroup);
        
    }

    createCanvas (id){
        
        const canvas = document.createElement('canvas');
        canvas.id     = id;
        canvas.width  = window.innerWidth;
        canvas.height = window.innerHeight;
        canvas.classList.add("canvas" ,  "position-absolute");
        this.parentElem.appendChild(canvas);
        return canvas;
    }
}


export {CanvasBuilder}